* {
  margin: 0;
  padding: 0;
}

.lp-container {
  max-width: 2560px;
  margin: 0 auto;
  position: relative;
}

.lp-pic {
  display: block;
  width: 100%;
  pointer-events: none;
}

.lp-pic-mb {
  display: none;
}

.lp-form {
  position: absolute;
  width: 42%;
  left: 51.7%;
  bottom: 18.8%;
}

.lp-form #lp-email {
  width: 66%;
  display: inline-block;
  padding: 10px;
  box-sizing: border-box;
  outline: none;
  border: none;
}

.lp-form .input-group-btn {
  width: 30%;
  display: inline-block;
}

.lp-form .subscribe-email-submit {
  width: 100%;
  height: 100%;
  padding: 10px 0;
  box-sizing: border-box;
  cursor: pointer;
  outline: none;
  letter-spacing: 1px;
  background: #205dab;
  color: #fff;
  font-weight: bold;
  border: none;
}

@media (max-width: 767px) {
  .lp-pic-pc {
    display: none;
  }
  .lp-pic-mb {
    display: block;
  }
  .lp-form {
    width: 70%;
    left: 23.7%;
    bottom: 6.8%;
  }
  .lp-form #lp-email {
    padding: 6px;
    width: 62%;
    font-size: 12px;
  }
  .lp-form .input-group-btn {
    width: 34%;
  }
  .lp-form .subscribe-email-submit {
    padding: 6px 0;
    font-size: 12px;
    letter-spacing: 0;
  }
}
